<template>
  <div class="case">
    <mHeader goBack="true" ref="child1" :head-title="$t('individuation.caseTitle')"></mHeader>
    <goTop/>
    <div class="caseTop">
      <caseTop :head-top="$t('individuation.caseTop')"></caseTop>
    </div>
    <div class="caseContent">
      <!--tab-->
      <van-tabs :active="active" class="caseVantTab">
        <van-tab>
          <div slot="title">
            <goodsTitle :goods-title="$t('other.goods')"></goodsTitle>
          </div>

          <div class="tabList">
            <div class="case1">
              <ModuleOne
                :titleDt="$t('individuation.pimotitleDt')"
                :titleDd="$t('individuation.pimotitleDd')"
                :titleSpan="$t('individuation.pimotitleSpan')"
                p1="true"
              />
            </div>
            <div class="case2">
              <moduleTwo
                :twoP="$t('individuation.pimttwoP')"
                :twoSpan="$t('individuation.pimttwoSpan')"
                :twoSpan2="$t('individuation.pimttwoSpan2')"
                p2="true"
                p3="true"
              />
            </div>
            <div class="case3">
              <ModuleThree
                :threeP="$t('individuation.pimtthreeP')"
                :threeSpan="$t('individuation.pimtthreeSpan')"
                p4="true"
              />
            </div>
          </div>
        </van-tab>


        <van-tab>
          <div slot="title">
            <goodsTitle :goods-title="$t('other.details')"></goodsTitle>
          </div>
          <div class="tabList2">
            <ModuleTen3
              :tenDt1="$t('individuation.pimt3tenDt1')"
              :tenDd1="$t('individuation.pimt3tenDd1')"
              :tenDt2="$t('individuation.pimt3tenDt2')"
              :tenDd2="$t('individuation.pimt3tenDd2')"
              :tenDt3="$t('individuation.pimt3tenDt3')"
              :tenDd3="$t('individuation.pimt3tenDd3')"
              :tenDt4="$t('individuation.pimt3tenDt4')"
              :tenDd4="$t('individuation.pimt3tenDd4')"
              :tenDt5="$t('individuation.pimt3tenDt5')"
              :tenDd5="$t('individuation.pimt3tenDd5')"
              :tenDt6="$t('individuation.pimt3tenDt6')"
              :tenDd6="$t('individuation.pimt3tenDd6')"
              :tenDt7="$t('individuation.pimt3tenDt7')"
              :tenDd7="$t('individuation.pimt3tenDd7')"
              :tenDt8="$t('individuation.pimt3tenDt8')"
              :tenDd8="$t('individuation.pimt3tenDd8')"
              :tenDt9="$t('individuation.pimt3tenDt9')"
              :tenDd9="$t('individuation.pimt3tenDd9')"
            />
          </div>
        </van-tab>

      </van-tabs>
    </div>
    <mFooter/>
  </div>
</template>

<script>
  import mHeader from '../../../components/header/headerTab.vue'
  import caseTop from '../../../components/caseTopScreen.vue'
  import goodsTitle from '../../../components/goods.vue'
  import mFooter from '../../../components/footer/footer.vue'

  import ModuleOne from "../../../components/module/moduleOne.vue"
  import ModuleTwo from "../../../components/module/ModuleTwo.vue"
  import ModuleThree from "../../../components/module/ModuleThree.vue"
  import ModuleTen3 from "../../../components/module/ModuleTen3.vue"
  import goTop from '../../../components/goTop.vue';
  export default {
    name: 'page-tab-container',
    data() {
      return {
        active: 0,
        // listProcess:[{'dd':'派对酒水由JSS特供，除葡萄酒、洋酒、啤酒外，还有专业调酒师现场调酒。甜品台由Fano abbey（梵诺庄园）提供，高级定制翻糖蛋糕、下午茶。泳池派对布置，甜品台摆盘服务。','dt':'餐饮'},{'dd':'场地选择了北京金融街洲际酒店的游泳池，独特的半露天设计，保证更多的新鲜空气和阳光畅然流动，户外露台为活动增添更多可能。','dt':'场地'},{'dd':'酒店入口显示屏欢迎，夏日主题布置，另设甜品区、DJ台和小舞台。','dt':'布置'},{'dd':'商务巴车接送，保证学生安全。','dt':'交通'},{'dd':'JSS特供夏日主题花卉。','dt':'花卉'},{'dd':'专业救生员支持。','dt':'安保'},{'dd':'专业摄影师跟拍，捕捉精彩瞬间。另有摄像师全程记录并剪辑，根据需求制作不同风格VCR。','dt':'记录'},{'dd':'提供多媒体播放设备、世界百大DJ主持派对。','dt':'娱乐'},{'dd':'海报设计、易拉宝、KT板设计和制作。','dt':'物料设计'}]
      };
    },
    mounted () {
      window.addEventListener('scroll', this.handleScroll)
      this.handleScroll();
    },
    destroyed () {
      // 离开页面移除
      window.removeEventListener('scroll', this.handleScroll)
    },
    methods:{
      handleClick(tab, event) {
        console.log(tab, event);
      },
      handleScroll() {
        let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
        //console.log(scrollTop);
        let opcaity = (scrollTop / 100 > 1) ? 1 : scrollTop / 100;
        this.$refs.child1.headerBac(opcaity);
      }
    },
    components: {
      mHeader,
      mFooter,
      caseTop,
      goodsTitle,
      ModuleOne,
      ModuleTwo,
      ModuleThree,
      ModuleTen3,
      goTop
    }
  }
</script>
<style lang="scss" scoped>
  @import "../../../style/mixin";
  .case{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 102;
    background: #070705;
    .caseTop{
      padding-top: .88rem;
      width: 100%;
    }
    .caseContent{
      .caseVantTab{
        height: 100%;
        .tabList{
          background: #fff;
          padding-top: 2rem;
          .case2{
            margin-top: 1rem;
          }
          .case3{
            padding-top: 1rem;
            padding-bottom: .8rem;
          }
        }
        .tabList2{
          background: #fff;
          padding-top: 2rem;
        }
      }
    }
  }

</style>



